.App {
  text-align: left;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-private {
  margin: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App-login {
  margin: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

input, button {
  margin: 8px;
  width: 300px;
}

.App-error {
  margin-top: 8px;
  color: darkred;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.App-sidePanel {
  background-color: #f5f5f5;
  border-right: 1px solid gray;
}

.App-main {
  background-color: white;
}

.physList-physician {
  padding: 10px;
  cursor: pointer;
  width: 100%;
}

.physList-physician:hover {
  background-color: #0073e5;
  color: white;
}